.btn__icon {
  width: 24px;
  height: 16px;
  transition: all .2s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: relative;
}

.btn__icon > span, .btn__icon:before, .btn__icon:after {
  width: 100%;
  height: 10%;
  background-color: currentColor;
  transition: transform .2s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  left: 0;
}

.btn__icon:before, .btn__icon:after {
  content: " ";
}

.btn__icon > span {
  top: 50%;
  transform: scale(1)translateY(-50%);
}

.btn__icon:before {
  top: 0;
}

.btn__icon:after {
  bottom: 0;
}

.btn__icon:hover {
  color: #2196f3;
}

.menu__btn.active .btn__icon > span {
  transform-origin: 0;
  transform: scale(0);
}

.menu__btn.active .btn__icon:before {
  transform-origin: 0 0;
  transform: rotate(38deg);
}

.menu__btn.active .btn__icon:after {
  transform-origin: 0 100%;
  transform: rotate(-38deg);
}

